html,
body,
#root {
  height: 100%;
  width: 100%;
  overflow-x: hidden;
  font-family: 'Oswald', sans-serif;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  display: block;
  margin: auto;
  width: fit-content;
  text-align: center;
}

.app textarea {
  position: relative;
  left: -10px;
  display: block;
  width: 107%;
  border-radius: 15px;
  font-size: 20px;
  padding: 0.2em;
}

.app .artemiy {
  border-radius: 10%;
  border: 2px solid black;
}

footer {
  position: fixed;
  margin: 0;
  bottom: 0;
  left: 0;
  height: 40px;
  width: 100%;
  background: #0f0f0f;
  color: #fff;
}

footer span {
  position: relative;
  top: 10px;
}

footer span a {
  text-decoration: none;
  color: #fff;
}
